/* eslint-disable no-unused-vars */
import React, { useState } from "react";
import { useAlert } from "react-alert";
import { useForm } from "react-hook-form";
import { useLocation, useNavigate } from "react-router-dom";
import { forgetPassword, verifyOtp } from "../apiServices/home/homeHttpService";
import { showAlert } from "./commonComponent/alertManager";
import { RotatingLines } from "react-loader-spinner";
import OtpTimer from "otp-timer";

function Verification() {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    mode: "onChange",
  });
  const [loader, setLoader] = useState(false);
  const alert = useAlert();
  const navigate = useNavigate();
  const location = useLocation();

  const onSubmit = async (data) => {
    setLoader(true);
    let otp = Object.values(data);
    otp = otp.join("");

    const formData = {
      email: location?.state?.email,
      otp: otp,
    };

    const response = await verifyOtp(formData);
    if (!response.error) {
      navigate("/admin/reset-password", {
        state: {
          email: location?.state?.email,
        },
      });
      showAlert(alert, response.message, { timeout: 3000 });
      setLoader(false);
      reset();
    } else {
      showAlert(alert, response.message, { timeout: 3000 });
      setLoader(false);
    }
  };

  const moveOnMax = (event, field, nextField, preField) => {
    console.log(event.key, event.keyCode);
    event = event || window.event;
    if (event.keyCode !== 9) {
      if (event.key === "ArrowLeft" || event.key === "Backspace") {
        preField?.focus();
      } else {
        if (field?.value?.length >= field.maxLength) {
          nextField.focus();
        }
      }
    }
  };

  const resend = async () => {
    const formData = {
      email: location?.state.email,
    };

    const response = await forgetPassword(formData);
    if (!response.error) {
      showAlert(alert, response.results.otp, { timeout: 3000 });
    } else {
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };

  return (
    <>
      <section className="login_page">
        <div className="container-fluid px-0">
          <div className="row justify-content-center">
            <div className="col-xxl-4 col-xl-4 col-lg-6 col-md-10 col-11">
              <div className="login_top shadow">
                <div className="login_page_form">
                  <div className="row">
                    <div className="col-12 formheader mb-4 text-center">
                      {/* <div class="text-center mb-4">
                                       <img src="assets/img/logo.png" alt="">
                                    </div> */}
                      <h1>Verification</h1>
                      <p>Please enter the OTP received on your Email Address</p>
                    </div>
                    <div className="col-12">
                      <form
                        className="row form-design"
                        onSubmit={handleSubmit(onSubmit)}
                      >
                        <div className="form-group col-12">
                          <label htmlFor>OTP</label>
                          <div className="row">
                            <div className="col-3">
                              <input
                                type="number"
                                className="form-control text-center"
                                maxLength="1"
                                placeholder=""
                                name="otp11"
                                id="otp11"
                                {...register("otp11", { required: true })}
                                onKeyUp={(event) => {
                                  moveOnMax(
                                    event,
                                    document.getElementById("otp11"),
                                    document.getElementById("otp22")
                                  );
                                }}
                                onInput={(e) => {
                                  if (e.target.value.length > 1)
                                    e.target.value = e.target.value.slice(0, 1);
                                }}
                              />
                            </div>
                            <div className="col-3">
                              <input
                                type="number"
                                className="form-control text-center"
                                maxLength="1"
                                placeholder=""
                                name="otp22"
                                id="otp22"
                                {...register("otp22", { required: true })}
                                onKeyUp={(event) => {
                                  moveOnMax(
                                    event,
                                    document.getElementById("otp22"),
                                    document.getElementById("otp33"),
                                    document.getElementById("otp11")
                                  );
                                }}
                                onInput={(e) => {
                                  if (e.target.value.length > 1)
                                    e.target.value = e.target.value.slice(0, 1);
                                }}
                              />
                            </div>
                            <div className="col-3">
                              <input
                                type="number"
                                className="form-control text-center"
                                maxLength="1"
                                placeholder=""
                                name="otp33"
                                id="otp33"
                                {...register("otp33", { required: true })}
                                onKeyUp={(event) => {
                                  moveOnMax(
                                    event,
                                    document.getElementById("otp33"),
                                    document.getElementById("otp44"),
                                    document.getElementById("otp22")
                                  );
                                }}
                                onInput={(e) => {
                                  if (e.target.value.length > 1)
                                    e.target.value = e.target.value.slice(0, 1);
                                }}
                              />
                            </div>
                            <div className="col-3">
                              <input
                                type="number"
                                className="form-control text-center"
                                maxLength="1"
                                placeholder=""
                                name="otp44"
                                id="otp44"
                                {...register("otp44", { required: true })}
                                onKeyUp={(event) => {
                                  moveOnMax(
                                    event,
                                    "",
                                    "",
                                    document.getElementById("otp33")
                                  );
                                }}
                                onInput={(e) => {
                                  if (e.target.value.length > 1)
                                    e.target.value = e.target.value.slice(0, 1);
                                }}
                              />
                            </div>
                          </div>
                        </div>
                        <div className="time_js text-center mb-4">
                          <OtpTimer
                            seconds={30}
                            minutes={0}
                            style={{ marginTop: "-100px" }}
                            resend={() => resend()}
                            ButtonText="Resend OTP"
                          />
                        </div>
                        <div className="form-group col-12 mb-0 mt-2">
                          <button
                            type="submit"
                            className="comman_btn"
                            disabled={loader}
                          >
                            {loader ? (
                              <div>
                                <span className="me-2">Wait</span>
                                <RotatingLines
                                  strokeColor="white"
                                  strokeWidth="5"
                                  animationDuration="0.75"
                                  width="20"
                                  visible={true}
                                />
                              </div>
                            ) : (
                              <span>Submit</span>
                            )}
                          </button>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default Verification;
